import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TableRow from "@mui/material/TableRow";

const dispatchedEvents = [];

const SrsMuiTableRow = function (props, ref) {
  return (
    <TableRow {...props} theme={props.muiTheme}>
      <slot />
    </TableRow>
  );
};

const styles = `
  /* Custom styles for the MuiTableRow component */
`;

const props = [
  {
    name: "hover",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "selected",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to TableRow if needed
];

const slots = [
  {
    name: "default",
    allowed: ["srs-table-cell"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTableRow),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
